import "./styles.css";
import * as React from "react";
import Mond from "./mond";
import Riue from "./riue";
import Inazuma from "./inazuma";
import Sumeru from "./sumeru";

import Sword from "./sword";
import Clymore from "./claymore";
import Bow from "./bow";
import Catalyst from "./catalyst";
import Polearm from "./polearm";

import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";

export default function BasicCard() {
  return (
    <>
      <h1 href="#top">原神　強化素材一覧(PHONE)</h1>

      <Stack direction="row" spacing={2}>
        <Button variant="contained" href="#chara">
          キャラクター
        </Button>
        <Button variant="contained" href="#weapon">
          武器
        </Button>
      </Stack>

      <h2 id="chara">キャラクター</h2>

      <Stack direction="row" spacing={2}>
        <Button variant="contained" href="#mond">
          モンド
        </Button>
        <Button variant="contained" href="#riue">
          璃月
        </Button>
        <Button variant="contained" href="#inazuma">
          稲妻
        </Button>
        <Button variant="contained" href="#sumeru">
          スメール
        </Button>
      </Stack>
      <h2 id="mond">モンド</h2>
      <Mond />

      <Stack direction="row" spacing={2}>
        <Button variant="contained" href="#top">
          トップに戻る
        </Button>
      </Stack>

      <h2 id="riue">璃月</h2>
      <Riue />

      <Stack direction="row" spacing={2}>
        <Button variant="contained" href="#top">
          トップに戻る
        </Button>
      </Stack>

      <h2 id="inazuma">稲妻</h2>
      <Inazuma />

      <Stack direction="row" spacing={2}>
        <Button variant="contained" href="#top">
          トップに戻る
        </Button>
      </Stack>

      <h2 id="sumeru">スメール</h2>
      <Sumeru />

      <Stack direction="row" spacing={2}>
        <Button variant="contained" href="#top">
          トップに戻る
        </Button>
      </Stack>

      <h2 id="weapon">武器</h2>

      <Stack direction="row" spacing={2}>
        <Button variant="contained" href="#sword">
          片手剣
        </Button>
        <Button variant="contained" href="#claymore">
          両手剣
        </Button>
        <Button variant="contained" href="#bow">
          弓
        </Button>
        <Button variant="contained" href="#catalyst">
          法器
        </Button>
        <Button variant="contained" href="#polearm">
          長柄武器
        </Button>
      </Stack>

      <h2 id="sword">片手剣</h2>
      <Sword />

      <Stack direction="row" spacing={2}>
        <Button variant="contained" href="#top">
          トップに戻る
        </Button>
      </Stack>

      <h2 id="claymore">両手剣</h2>
      <Clymore />

      <Stack direction="row" spacing={2}>
        <Button variant="contained" href="#top">
          トップに戻る
        </Button>
      </Stack>

      <h2 id="bow">弓</h2>
      <Bow />

      <Stack direction="row" spacing={2}>
        <Button variant="contained" href="#top">
          トップに戻る
        </Button>
      </Stack>

      <h2 id="catalyst">法器</h2>
      <Catalyst />

      <Stack direction="row" spacing={2}>
        <Button variant="contained" href="#top">
          トップに戻る
        </Button>
      </Stack>

      <h2 id="polearm">長柄武器</h2>
      <Polearm />

      <Stack direction="row" spacing={2}>
        <Button variant="contained" href="#top">
          トップに戻る
        </Button>
      </Stack>
    </>
  );
}
